<template>
    <div class="checkHouseWork" >
        <navigation-bar pageName="验房报修" @onLeftClick="goBack">
        </navigation-bar>
        <div class="checkList" v-show="checkList.length > 0">
            <van-list
                v-model="loading"
                :finished="finished"
                :finished-text="finishedText"
                :immediate-check="false"
                @load="loadMore">
                <div class="item" v-for="(item,index) in checkList" :key="index" @click="goDetail(item)">
                    <div class="title">
                        <div class="top">
                            <img :src="workIcon" alt="">
                            <div class="building">{{item.areaName}} {{item.buildName}} {{item.unitNo}} {{item.roomName}}</div>
                        </div>
                        <div class="fontRed status" v-if="item.status == '0'">待受理</div>
                        <div class="fontGreen status" v-if="item.status == '1'">受理中</div>
                        <div class="fontYellow status" v-if="item.status == '2' && (item.closeTye != '1' && item.closeTye != '2')">待评价</div>
                        <div class="fontBlack status" v-if="item.status == '2' && item.closeTye == '1'">暂时关闭</div>
                        <div class="fontBlack status" v-if="item.status == '2' && item.closeTye == '2'">非正常关闭</div>
                        <div class="fontBlack status" v-if="item.status == '3' && item.closeTye != '2'">已结束</div>
                        <div class="fontBlack status" v-if="item.status == '3' && item.closeTye == '2'">非正常关闭</div>
                    </div>
                    <div class="content">
                        <div class="img-box">
                            <img v-bind:src="(item.picUrl && item.picUrl.length > 0) ? item.picUrl.split(',')[0] : defaultImg" alt="" v-on:error.once="showErrorImg($event)">
                        </div>
                        <div class="dis-box">
                            <div style="display: flex; justify-content: space-between;width: 100%;">
                              <div class="redTip" v-if="item.typeFlag == 1">投诉</div>
                              <div class="redTip" v-if="item.typeFlag == 2 || item.typeFlag == 0">报修</div>
                              <img class="content-duban" v-bind:src="dubanIcon" v-if="item.userPosition && item.userPosition != 1" alt="">
                            </div>
                            <div class="content-dis">{{item.content}}</div>
                        </div>
                    </div>
                    <div class="time" v-if="item.status != '2' && item.status != '3'">
                      <span v-if=" !item.compLimitTime && item.confLimitTime">确认时限: {{item.confLimitTime}}</span>
                      <span v-if="item.compLimitTime && item.confLimitTime && item.STATUS == '1'">处理时限: {{item.compLimitTime}}</span>
                      <span v-if="item.compLimitTime && item.confLimitTime && item.STATUS == '2'">完成时限: {{item.compLimitTime}}</span>
                    </div>
                </div>
            </van-list>
        </div>

        <div class="no-list" v-show="checkList.length == 0">
          <img :src="noSourceImg" alt="">
          <div>暂无工单</div>
        </div>
    </div>
</template>

<script>
import { getCheckHouseListNew } from "../../services/checkHouseService";
import { store } from "../../utils/store";
import workIcon from "@/assets/imgs/checkHouse/work.png";
import defaultImg from "@/assets/imgs/workPlan/imgExample.png";
import finishImg from "@/assets/imgs/workPlan/finishLogo.png";
import noSourceImg from "@/assets/imgs/workPlan/noSource.png";
import leftIcon from "@/assets/imgs/workPlan/left_black.png";
import log from "../../utils/log";
import dubanImg from "@/assets/imgs/workPlan/duban.png";
import NavigationBar from "../title/NavigationBar";

export default {
  name: "checkHouseWork",
  components: { NavigationBar },
  data() {
    return {
      activeTab: 0,

      // 上拉加载是否处于加载状态
      loading: false,

      // 上拉加载是否加载完成
      finished: false,
      finishedText: "我是有底线的",

      // 工单列表
      checkList: [],

      // 获取接口的请求参数
      requestData: {
        typeFlag: '2',
        userCode: null,
        page: 1, //分页页号
        row: 10 //每页条数
      },

      // 图片
      workIcon: workIcon,
      defaultImg: defaultImg,
      finishImg: finishImg,
      noSourceImg: noSourceImg,
      dubanIcon: dubanImg,
      leftIcon: leftIcon,
      scrollTop: 0
    };
  },
  beforeRouteLeave(to, from, next) {
    if(to.name != "checkHouse"){
      this.scrollTop = document.querySelector(".checkList").scrollTop;
    }
    next();
  },
  activated () {
    this.requestData.userCode = store.get("userInfo").USER_CODE;
    this.requestData.areaCode = store.get("userInfo").AREA_CODE;
    var roomCode = store.get("checkHouseWorkRoomCode");
    if(roomCode){
      this.resetData();
      this.requestData.userCode = store.get("userInfo").USER_CODE;
      this.requestData.areaCode = store.get("userInfo").AREA_CODE;
      this.requestData.roomCode = roomCode;
      this.getCheckHouseList();
      store.set("checkHouseWorkRoomCode", '');
      document.querySelector(".checkList").scrollTop = 0;
    } else {
      document.querySelector(".checkList").scrollTop = this.scrollTop;
    }
  },
  methods: {
    resetData: function() {
        this.loading = false;
        this.finished = false;
        this.finishedText = "我是有底线的";
        this.checkList = [];
        this.requestData = {
          typeFlag: '2',
          userCode: null,
          page: 1, //分页页号
          row: 10 //每页条数
        };
    },
    showErrorImg: function(event) {
      event.currentTarget.src = this.defaultImg;
    },

    /**
     * 获取延时申请列表
     */
    getCheckHouseList: function() {
      this.loading = true;
      getCheckHouseListNew(this.requestData).then(
        res => {
          this.setCheckHouseList(res.data);
        },
        error => {
          this.setErrorInfo(error);
        }
      );
    },

    /**
     * 设置展示的工单列表信息
     */
    setCheckHouseList(res) {
      this.loading = false;
      this.finished = res.total <= this.requestData.page * this.requestData.row;
      if (this.requestData.page == 1) {
        this.checkList = res.list ? res.list : [];
      } else {
        this.checkList = this.checkList.concat(
          res.list ? res.list : []
        );
      }
      if (this.requestData.page == 1 && this.finished) {
        this.finished = true;
        this.finishedText = "";
      }
    },

    /**
     * 设置错误的处理信息
     */
    setErrorInfo(error) {
      this.loading = false;
      this.$toast(error);
      this.finished = true;
      this.finishedText = "";
    },

    /**
     * 上拉加载
     */
    loadMore: function() {
      this.requestData.page++;
      this.getCheckHouseList();
    },

    /**
     * 页面返回事件
     */
    goBack: function() {
      this.scrollTop = 0;
      store.set("reloadCheckHouseStatus", 0);
      this.$router.back(-1);
    },

    /**
     * 跳转到工单详情页面
     */
    goDetail: function(item) {
      store.set('workDetailComplCode', item.complCode);
      this.$router.push({
        name: "workDetail"
      });
    }
  },
  computed: {}
};
</script>
